* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  background-color: #eaeaea;
  transition: background-color .3s;
  position: relative;
}

.search-form {
  width: 100%;
  justify-content: center;
  margin: 20px 0;
  display: flex;
  position: relative;
}

.search-input {
  width: 50%;
  color: rgba(0, 0, 0, .9);
  background: rgba(255, 255, 255, .9);
  border: 4px solid rgba(0, 0, 0, .9);
  border-right: none;
  border-radius: 10px 0 0 10px;
  outline: none;
  padding: 10px;
  font-size: 20px;
}

.search-button {
  text-align: center;
  height: 51px;
  width: 40px;
  cursor: pointer;
  background: rgba(255, 255, 255, .9);
  border: 4px solid rgba(0, 0, 0, .9);
  border-radius: 0 10px 10px 0;
  outline: none;
  font-size: 20px;
  transition: all .5s;
}

.search-button:hover {
  fill: rgba(255, 255, 255, .9);
  background: rgba(0, 0, 0, .9);
}

.gallery {
  flex-wrap: wrap;
  display: flex;
}

.photo-card {
  width: calc(25% - 40px);
  height: 300px;
  border: 3px solid rgba(255, 255, 255, .9);
  border-radius: 10px;
  flex-direction: column;
  margin: 10px auto;
  text-decoration: none;
  transition: all .5s;
  display: flex;
  overflow: hidden;
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, .9);
  justify-content: space-between;
  display: flex;
}

.info-item {
  color: rgba(0, 0, 0, .9);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button-wrapper {
  width: 100%;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
}

.load-more {
  height: 50px;
  width: 200px;
  cursor: pointer;
  background: rgba(255, 255, 255, .9);
  border: 4px solid rgba(0, 0, 0, .9);
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-size: 20px;
  transition: all .5s;
  display: flex;
}

.load-more:hover {
  color: rgba(255, 255, 255, .9);
  background: rgba(0, 0, 0, .9);
  border-color: rgba(255, 255, 255, .9);
}

.is-hidden {
  visibility: hidden;
}

html {
  scroll-behavior: smooth;
}

.up {
  width: 70px;
  height: 70px;
  fill: rgba(0, 0, 0, .9);
  cursor: pointer;
  background: rgba(255, 255, 255, .9);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .5s ease-in-out;
  animation: bounce 2s ease-in-out infinite;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
  transform: translateY(0);
}

.up:hover {
  fill: rgba(255, 255, 255, .9);
  background: rgba(0, 0, 0, .9);
}

@keyframes bounce {
  0% {
    transform: translateY(10px);
  }

  25% {
    transform: translateY(-5px);
  }

  50% {
    transform: translateY(0);
  }

  75% {
    transform: translateY(-5px);
  }

  100% {
    transform: translateY(10px);
  }
}

.switcher {
  position: absolute;
  top: 0;
  right: 20px;
}

body.active {
  background-color: #333;
}

body.active .switcher-label .switcher-toggler {
  left: 60%;
}

input[type="checkbox"] {
  opacity: 0;
  visibility: hidden;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.moon {
  fill: silver;
}

.sun {
  fill: gold;
}

.switcher-label {
  cursor: pointer;
  width: 125px;
  height: auto;
  min-height: 55px;
  background-color: #111;
  border-radius: 30px;
  justify-content: space-between;
  align-items: center;
  padding: 2px 10px;
  display: flex;
  position: relative;
}

.switcher-label .switcher-toggler {
  width: 45px;
  height: 45px;
  z-index: 1;
  background-color: #fff;
  border-radius: 50%;
  transition: all .4s;
  display: block;
  position: absolute;
  top: 9%;
  left: 4%;
}

